import React from 'react';

const About = () => {
  return (
    <div className='container'>
      <h2>Vr Historie og Felleskap p Veien</h2>
      <p>Heiersmed'n MC Touring ble grunnlagt i 2020, da en liten gjeng med familie og venner mttes for sin frste felles tur p Gvarv i Telemark. Ideen om  starte en klubb oppsto ut fra familiens kende interesse for motorsykkel, og nsket om  ha en sosial arena der vi kunne dele gleden over friheten p to hjul. Gjennom rene har vi pnet klubben for venner og nye medlemmer, og i dag nsker vi  vre et fellesskap som samler motorsykkelentusiaster p tvers av aldre og erfaring.</p>
      <h3>En pen Vei for Alle Motorsykkelentusiaster</h3>
      <p>Vi i Heiersmed'n MC Touring nsker  skape en pen og inkluderende klubb for alle som deler en lidenskap for motorsykler. Vr visjon er  vre en sosial arena der bde erfarne og nye entusiaster kan finne glede, trygghet og fellesskap p veien. Enten du kommer alene eller tar med familien, hper vi  tilby et fellesskap som verdsetter mangfold og en felles kjrlighet for motorsykkelkjring.</p>
      <h3>Et Fellesskap for Alle Aldre og Erfaringer</h3>
      <p>Hos Heiersmed'n MC Touring er alle som nsker en behagelig og koselig atmosfre, og som har en interesse for motorsykler, hjertelig velkomne. Vi er en klubb med et bredt aldersspekter, hvor bde unge og eldre finner sin plass. Enten du er ny p veien eller har mange rs erfaring, finner du et fellesskap som setter pris p mangfold og en inkluderende kjreopplevelse.</p>
      <h3>Eventyr p Veien  rlige Turer og Lokale Kjreturer</h3>
      <p>Vi har som ml  arrangere minst n rlig fellestur for hele klubben, slik at alle kan samles og dele opplevelsen. I tillegg arrangerer vi flere turer gjennom ret for de som har mulighet til  bli med. Vre turer gr ofte rundt i stfold, der strstedelen av klubben holder til, og p Vestlandet, hvor flere av vre medlemmer ogs er basert. Vi nsker  skape turer som alle kan delta p, uansett hvor de bor, og som styrker fellesskapet i klubben.</p>
      <h3>Trygge Turer, Gode Opplevelser</h3>
      <p>I Heiersmed'n MC Touring legger vi vekt p trygge og hyggelige kjreturer der vi tar oss god tid. Hos oss er det opplevelsen og gleden ved turen som str i fokus, ikke hastigheten. Vi kjrer sammen for hyggen og fellesskapet, og racing er derfor ikke en del av vre turer. Vrt ml er  sikre at alle, uansett erfaring, kan nyte en trygg og behagelig kjreopplevelse.</p>
    </div>
  );
}

export default About;
